import React, { useEffect, useState } from 'react';
import { observer } from 'mobx-react';
import Select from "react-select";
import Modal from '../modal/Modal';
import styles from './Dashboard.module.scss'
import store from '../../store/Store';
import PieChartRaw from '../../assets/fw/pieChart/pieChart';
// import Chart from '../../assets/fw/chart';

const Dashboard: React.FC = observer(() => {

  const [show,setShow] = useState(false) 

  const handleShowModal = () => {
    setShow(!show)
  }

  const getSum = (e:any)=>{
    
    let sum = [0,0,0]
    store.storeData.test=e

    store.storeData.trainer.data.filter((value:any)=>
    store.storeData.test.some((filter:any)=> 
    filter.label === value.number)).map((item:any,index:number)=>{
       Object.keys(item).map((key,id)=>{
          switch(item[key]){
            case 1: sum[0] += 1 
          break;
            case 2: sum[1] += 1 
          break;
            case 3: sum[2] += 1 
          break;
          }
        })
      })
      return sum
  }


  return <div>
    <div style={{display:'flex', margin: '32px'}}>
      <div  className={styles.filter}>
        <div className={styles.label_filter}></div>
        <Select
        isMulti
        placeholder={' '}
        onChange={(e)=>getSum(e)}
        // value={store.storeData.test}
        options={store.storeData.trainer.data.map((row:any,index:any) => {
          return {value: index, label:row.number}
        })}/>
      </div>
      <div  className={styles.filter}>
        <div className={styles.label_filter}></div>
        <Select
        isMulti
        placeholder={' '}
        options={store.storeData.trainer.headers.filter((header:string) => header !== '').map((header:any,id:number) => {
           return {value: id, label: header}
        })
        }/>
      </div>
      <div className={styles.button} onClick={()=> handleShowModal()}> </div>
    </div>
    <div style={{display: 'inline-block' ,background: 'white',margin:'32px'}}>
      <div className={styles.label}>  </div>
    <PieChartRaw
    e={store.storeData.test}
    sum={(e:any)=>getSum(e)}
    />
    </div>
  <Modal
  show={show} 
  handleShowModal={()=>handleShowModal()}/>
  </div>
});

export default Dashboard;