// import "./styles.css";
import { observer } from "mobx-react";
import React, { useCallback, useEffect, useState } from "react";
import { PieChart, Pie, Cell } from "recharts";
import store from "../../../store/Store";
import styles from './pieChart.module.scss' 

type propsType = {
  sum: (e:any)=>any
  e:any
}

const PieChartRaw = observer((props: propsType) => {

  const{sum,e} = props

const getSum =(mark:number)=>{
  let sum = 0
  store.storeData.trainer.data.map((item:any,index:number)=>{
     Object.keys(item).map((key,id)=>{
      if(item[key]===mark){
        sum += 1
      }
  })
})
console.log(sum)
return sum
}

const data = [
  { name: "  '1'", value: store.storeData.test === '' || store.storeData.test.length === 0 ? getSum(1): sum(e)[0]},
  { name: "  '2'", value: store.storeData.test === '' || store.storeData.test.length === 0 ? getSum(2): sum(e)[1]},
  { name: "  '3'", value: store.storeData.test === '' || store.storeData.test.length === 0 ? getSum(3): sum(e)[2]},
  // { name: "Group D", value: 200 }
];

const COLORS = ["#0088FE", "#00C49F", "#FFBB28"];

const RADIAN = Math.PI / 180;
const renderCustomizedLabel = ({
  cx,
  cy,
  midAngle,
  innerRadius,
  outerRadius,
  percent,
  index
}: any) => {
  const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
  const x = cx + radius * Math.cos(-midAngle * RADIAN);
  const y = cy + radius * Math.sin(-midAngle * RADIAN);

  return (
    <text
      x={x}
      y={y}
      fill="white"
      textAnchor={x > cx ? "start" : "end"}
      dominantBaseline="central"
    >
      {`${(percent * 100).toFixed(0)}%`}
    </text>
  );
};
// export default function App() {
  return (
    <div className={styles.body}>
    <PieChart width={400} height={400}>
      <Pie
        data={data}
        cx={200}
        cy={200}
        labelLine={false}
        label={renderCustomizedLabel}
        outerRadius={150}
        fill="#8884d8"
        dataKey="value"
      >
        {data.map((entry, index) => (
          <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
        ))}
      </Pie>
    </PieChart>
    <div style={{margin:'0 32px'}}>{
      data.map((item:any,id) => {
        return <div className={styles.legend_row} >
          <div className={styles.circle} style={{background: COLORS[id]}}></div>
          {item.name}: {item.value}</div>
      })
    }
    </div>
    </div>
  );
// }
})
export default PieChartRaw;