import React, {useState} from 'react';
import { observer } from 'mobx-react';
import mpgu_logo from '../../../assets/icons/mpgu_logo.png';
import styles from './ModalBlock.module.scss'
import cn from 'classnames'
import store from '../../../store/Store';
import close from '../../../assets/icons/Close.svg'
// import { useState } from 'react';
// import Header from '../header/Header';
// import {version} from '../../../config'

type propsType = {
  show: boolean;
  handleShowModal: ()=>void;
}

const ModalBlock = observer((props: propsType) => {

  const{show, handleShowModal} = props

  const [active,setActive] = useState([false,true]) 

  const handleSwitch = (index:number) => {
    switch(index){
      case 0:
        setActive([true,false])
      break;
      case 1:
        setActive([false,true])
      break;
    }
  }

  return <div className={cn(styles.body,!show && styles.disable)}>
      <div className={styles.header_body}>
        <div className={styles.header}>   </div>
        <div 
        className={cn(styles.button,active[0] && styles.button_avtive)}
        onClick={()=>handleSwitch(0)}></div>
        <div 
        className={cn(styles.button,active[1] && styles.button_avtive)}
        onClick={()=>handleSwitch(1)}></div>
        <img className={styles.img} onClick={()=>handleShowModal()} src={close} alt='img'/>
      </div>
      { active[1] &&
      <div className={styles.table}>
        <div
        className={cn(styles.table_row,styles.table_row_header)}
        style={{gridTemplateColumns: store.storeData.trainer.column}}>
          {
            store.storeData.trainer.headers.map((header:string,index:number)=>{
              return <div title={header} className={styles.table_cell}>{header}</div>
            })
          }
        </div>

        <div className={styles.body_row}>
      {
        store.storeData.trainer.data.map((row:any,id:number) => {
          return <div className={styles.table_row}
          style={{gridTemplateColumns: store.storeData.trainer.column}}>
            {
            Object.keys(row).map((item,index)=>{
              return <div className={cn(styles.table_cell)}>{row[item]}</div>
            })
          }
          </div>
      })
    }
      </div>
      </div>
    }
  </div>
});

export default ModalBlock;
