import React from 'react';
import { observer } from 'mobx-react';
import mpgu_logo from '../../../assets/icons/mpgu_logo.png';
import './HeaderLogo.scss'
// import {version} from '../../../config'

const HeaderLogo: React.FC = observer(() => {

  return <div className="header_logo">
    <img className="header_logo_img" src={mpgu_logo} alt="logo" />
    <span className="header_logo_title">
            
    </span>
  </div>
});

export default HeaderLogo;
